
import { defineComponent } from 'vue'
export default defineComponent({
  name: 'z-button',

  props: {
    type: {
      type: String,
      default: 'default',
      validator: (value: string) => {
        return ['success', 'danger', 'info', 'primary', 'warning', 'default'].indexOf(value) > -1
      }
    },

    size: {
      type: String,
      default: 'normal',
      validator: (value: string) => {
        return ['mini', 'small', 'large', 'normal'].indexOf(value) > -1
      }
    },

    round: {
      type: Boolean,
      default: false
    },

    circle: {
      type: Boolean,
      default: false
    },

    disabled: {
      type: Boolean,
      default: false
    },

    plain: {
      type: Boolean,
      default: false
    }
  },

  setup () {
    return {}
  }
})
